.open-modal {
    position: fixed;
    bottom: 10%;
    transform: translate(0, -10%);
    border: none;
    font-size: 35px;
    font-weight: 200;
    /* padding: 6px 16px; */
    position: flex;
    color: rgb(195, 98, 98);
    background-color: rgba(192, 192, 192, 0.398);
    border-radius: 50%;
    padding: 0;
    margin: 8px 0 8px 8px;
    height: 40px;
    width: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.button-container {
    margin: 0 10px;
    position: fixed;
    bottom: 10%;
    transform: translate(0, -10%);
    background-color: rgba(97, 95, 95, 0.14);
    box-shadow: 0 4px 30px rgba(76, 0, 255, 0.163);
    border: 1px solid rgba(97, 95, 95, 0.15);
    backdrop-filter: blur(1px);

    height: 40px;
    aspect-ratio: 1;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.open-modal-icon {
    position: fixed;
    /* bottom: 10%; */
    /* transform: translate(0, -10%); */
    /* border: none; */
    /* font-size: 35px; */
    /* font-weight: 200; */
    /* padding: 6px 16px; */
    position: flex;
    color: rgb(195, 98, 98);
    /* background-color: rgba(192, 192, 192, 0.398); */
    border-radius: 50%;
    padding: 0;
    /* margin: 8px 0 8px 8px; */
    height: 60%;
    aspect-ratio: 1;
    filter: invert(42%) sepia(62%) saturate(422%) hue-rotate(311deg) brightness(97%) contrast(94%);
}